import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { HomeProduct, Facade, Package } from '../../../../../../model/home-products/home-products-model';
import { FooterHeaderDataService } from '../../../../shared/footer-header-data.service';
import { RestServices } from '../../../../../../services/rest/rest-services.service';
import { ProgressProvider } from '../../../../shared/data/progress-provider';
import { ToastrService } from 'ngx-toastr';
import { Utilities } from '../../../../../../services/utils/utilities';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal/bs-modal.service';
import { TemplateRef } from '@angular/core/src/linker/template_ref';

@Component({
  selector: 'app-home-product-edit',
  templateUrl: './home-product-edit.component.html',
  styleUrls: ['./home-product-edit.component.scss']
})
export class HomeProductEditComponent implements OnInit {
  setHeader:any[] = [false,false,'Home Product','Edit',false,true,true,true,true,'disable'];
  setFooter:any[] = ['footer2',false,true,false,true];  

  homeProduct: HomeProduct;
  facades: Facade[];
  packages: Package[];

  homeProductId: number;

  filename:string = 'Browse';
  newFile: File;

  bsModalRef: BsModalRef;
  modalMessage: string;

  dataIndex = 0;
  deleteId: number;
  taskOwner = 'facade';

  // the item to be created or edited
  facade: Facade;
  package: Package;
  modalAddFilename = 'Browse';
  modalTitle = '';
  constructor(private router: Router, public dataService:FooterHeaderDataService, private activatedRoute: ActivatedRoute,
    private rest: RestServices, private toastr: ToastrService, private modalService: BsModalService) { 
    dataService.varHeader.next(this.setHeader);
    dataService.varFooter.next(this.setFooter);

    this.homeProduct = new HomeProduct();
    this.homeProduct.facades = [];
    this.homeProduct.packages = [];
    
    this.facades = [];
    this.packages = [];

    this.activatedRoute.params.subscribe(params => {
      this.homeProductId = params['id'];
    });
  }

  ngOnInit() {
    this.ini();
  }

  ini() {
    ProgressProvider.startLoading();
    this.rest.GET('home_products/' + this.homeProductId).then((data: any) => {
     // console.log(data.data);
      this.homeProduct.home_design_id = data.data.home_design_id;
      this.homeProduct.base_price = data.data.base_price;
      this.homeProduct.description = data.data.description;
      this.homeProduct.design_code = data.data.design_code;
      this.homeProduct.facades = [];
      this.homeProduct.packages = [];
      this.homeProduct.home_design_name = data.data.home_design_name;
      this.homeProduct.levels = data.data.levels;
      // packages
      for (var i = 0; i < data.data.package_infos.length; i++) {
        let pkg = new Package();
        pkg.description = data.data.package_infos[i].description;
        pkg.package_code = data.data.package_infos[i].package_code;
        pkg.package_id = data.data.package_infos[i].package_id;
        pkg.package_name = data.data.package_infos[i].package_name;
        pkg.package_type = data.data.package_infos[i].package_type;
        pkg.price = data.data.package_infos[i].price;
        pkg.sub_id = data.data.package_infos[i].sub_id;

        this.packages.push(pkg);
        this.homeProduct.packages.push({'package_id': pkg.package_id});
      }
      // facades
      for (var i = 0; i < data.data.space_infos.length; i++) {
        let fcd = new Facade();
        fcd.description = data.data.space_infos[i].description;
        fcd.space_id = data.data.space_infos[i].space_id;
        fcd.space_name = data.data.space_infos[i].space_name;
        fcd.space_type_id = data.data.space_infos[i].space_type_id;
        fcd.sub_id = data.data.space_infos[i].sub_id;

        this.facades.push(fcd);
        this.homeProduct.facades.push({'space_id': fcd.space_id});
      }
      if (data.data.media_files_info) {
        this.filename = data.data.media_files_info.media_name;
      }

      ProgressProvider.stopLoading();
    });
  }

  fileChanged(event) {
    this.filename = event.srcElement.files[0].name;
    this.newFile =  event.srcElement.files[0];
  }

  openModal(template: TemplateRef<any>, id: number, index: number, taskOwner: string) {
    this.deleteId = id;
    this.dataIndex = index;
    this.taskOwner = taskOwner;
    if (taskOwner == 'facade') {
      this.modalMessage = 'Are you sure to delete this facade?';
    }
    else {
      this.modalMessage = 'Are you sure to delete this package?'
    }
    this.bsModalRef = this.modalService.show(template);
  }

  openAddModal(template: TemplateRef<any>, taskOwner: string) {
    this.modalAddFilename = 'Browse';
    if (taskOwner == 'facade') {
      this.facade = new Facade();
      this.modalTitle = 'Add Facade Design';
    } 
    else {
      this.package = new Package();
      this.modalTitle = 'Add Package  Design';
    }

    this.bsModalRef = this.modalService.show(template);
  }

  openEditModal(template: TemplateRef<any>, taskOwner: string) {
    this.modalAddFilename = 'Browse';
    if (taskOwner == 'facade') {
      this.facade = new Facade();
      this.modalTitle = 'Edit Facade Design';
    } 
    else {
      this.package = new Package();
      this.modalTitle = 'Edit Package  Design';
    }

    this.bsModalRef = this.modalService.show(template);
  }

  modalFileChanged(event, taskOwner: string) {
    this.modalAddFilename = event.srcElement.files[0].name;
    if (this.taskOwner == 'facade') {
      this.facade.file =  event.srcElement.files[0];
    }
    else {
      this.package.file =  event.srcElement.files[0];
    }
  }

  deleteItem() {
    this.bsModalRef.hide();
    console.log(this.deleteId);
    ProgressProvider.startLoading();
    if (this.taskOwner == 'facade') {

    }
    else {

    }
    ProgressProvider.stopLoading();
  }

  removeImage(taskOwner: string) {
    if (taskOwner == 'facade') {
      this.modalAddFilename = 'Browse';
      this.facade.file = null;
    }
    else if (taskOwner == 'package') {
      this.modalAddFilename = 'Browse';
      this.package.file = null;
    }
    else {
      this.filename = 'Browse';
      this.newFile = null;
    }
  }

  onSubmit() {
    if (!this.homeProduct.home_design_name) {
      this.toastr.error(Utilities.getToastContentSuccess("Home product name is required."), '', { closeButton: true, enableHtml: true });
      return;
    }
    ProgressProvider.startLoading();
    this.rest.UpdateHomeProducts(this.homeProductId, this.homeProduct, this.newFile).then((response: any) => {
      ProgressProvider.stopLoading();
      if (response.status) {
        this.toastr.success(Utilities.getToastContentSuccess("Item updated successfully"), '', { closeButton: true, enableHtml: true })
        this.ini();
      }
      else {
        this.toastr.error(Utilities.getToastContentSuccess("Unable to update the item. Please try again"), '', { closeButton: true, enableHtml: true })
      }
    });
  }

  modalSubmit() {
    console.log('modal Submit');

    this.bsModalRef.hide();
  }
}



