import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { FooterHeaderDataService } from '../../../shared/footer-header-data.service';

import { environment } from '../../../../../../environments/environment';
import { Http } from '@angular/http/src/http';
import { HttpClient } from '@angular/common/http/src/client';
import { ToastNotificationComponent } from '../../../shared/toast-notification/toast-notification.component';
import { HomeProduct, Facade, Package } from '../../../../../model/home-products/home-products-model';
import { TemplateRef } from '@angular/core/src/linker/template_ref';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal/bs-modal.service';
import { RestServices } from '../../../../../services/rest/rest-services.service';
import { PopupSearchData } from '../../../../../model/popp-search/popup-search-model';
import { ProgressProvider } from '../../../shared/data/progress-provider';
import { AppModule } from '../../../../../app.module';

import { ToastrService } from 'ngx-toastr';
import { Utilities } from '../../../../../services/utils/utilities';
import { Location } from '@angular/common';

@Component({
  selector: 'app-home-product-create',
  templateUrl: './home-product-create.component.html',
  styleUrls: ['./home-product-create.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HomeProductCreateComponent implements OnInit {
  setHeader:any[] = [false,false,'Home Product','Create New',false,true,true,true,true,'disable'];
  setFooter:any[] = ['footer2',false,true,false,true];  

  @ViewChild('AddFacadeModal') AddFacadeModal;
  @ViewChild('AddPackageModal') AddPackageModal;

  filename = 'Browse';
  f_filename = 'Browse';
  p_filename = 'Browse';
  loadedData: PopupSearchData[] = []; // for displaying list of facades

  facades: Facade[];
  packages: Package[];

  bsModalRef: BsModalRef;

  homeProduct: HomeProduct; // the main data container


  facade: Facade; // when adding facade
  package: Package; // when adding pakage

  homeProductImage: File;

  // when selecting facade
  selectedData: PopupSearchData;

  selectMode = 'facade';

  facadeOperationMode = 'add';
  constructor(public dataService:FooterHeaderDataService, private modalService: BsModalService,
  private rest: RestServices, private toastr: ToastrService, private location: Location) { 
    dataService.varHeader.next(this.setHeader);
    dataService.varFooter.next(this.setFooter);

    this.facades = [];
    this.packages = [];
    this.homeProduct = new HomeProduct();
  
    this.homeProduct.facades = [];
    this.homeProduct.packages = [];

  }

  // when browsing image file
  fileChanged(event, index: number) {
    switch (index) {
      case 1:
        this.filename = event.srcElement.files[0].name;
        this.homeProductImage =  event.srcElement.files[0];
      break;
      case 2:
        this.f_filename = event.srcElement.files[0].name;
        this.facade.file = event.srcElement.files[0];
      break;
      case 3:
        this.p_filename = event.srcElement.files[0].name;
        this.package.file = event.srcElement.files[0];
      break;
      default:
      break;
    }
  }

  ngOnInit() {
  }

  onSubmit() {
    if (this.homeProduct.home_design_name) {
      ProgressProvider.startLoading();
      this.homeProduct.has_media = this.homeProductImage != null;
      this.rest.AddHomeProducts(this.homeProduct, this.homeProductImage).then((data: any) => {
        ProgressProvider.stopLoading();
        if (data.status) {
          this.toastr.success(Utilities.getToastContentSuccess("New product has been successfully added."), '', { closeButton: true, enableHtml: true});
          this.location.back();
        }
        else {
          this.toastr.error(Utilities.getToastContentError("An error occured while adding the item. Please try again."), '', { closeButton: true, enableHtml: true });
        }
      });
    }
    else {
      this.toastr.error(Utilities.getToastContentError("Product name is required."), '', { closeButton: true, enableHtml: true });
    }
  }

  removeImage(index: number) {
    switch (index) {
      case 1:
      this.filename = 'Browse';
      this.homeProductImage = null;
      break;
      case 2:
      this.f_filename = 'Browse';
      this.facade.file = null;
      break;
      case 3:
      this.p_filename = 'Browse';
      this.package.file = null;
      break;
      default:
      break;
    }
  }

  openModal(template: TemplateRef<any>, index: number) {
    switch (index) {
      case 1:
       
      break;
      case 2:
        this.resetFacade();
      break;
      case 3:
      this.resetPackage();
        // this.selectMode = 'package';
        // this.preparePackages();
      break;
      case 4:
        this.selectMode = 'facade';
        this.prepareFacades();
      break;
      default:
      break;
    }
    this.bsModalRef = this.modalService.show(template);
  }

  addFacade() {
    this.facades.push(this.facade);
    this.resetFacade();
    this.bsModalRef.hide();
  }

  addPackage() {
    this.packages.push(this.package);
    this.resetPackage();
    this.bsModalRef.hide();
  }

  removeFacade(index: number) {
    this.facades.splice(index, 1);
  }
  removePackage(index: number) {
    this.packages.splice(index, 1);
  }
  resetFacade() {
    this.facade = new Facade();
    this.facade.description = '';
    this.facade.space_name = '';
    this.f_filename = 'Browse';
  }
  resetPackage() {
    this.package = new Package();
    this.package.description = '';
    this.package.package_name = '';
    this.p_filename = 'Browse';
  }
  onItemSelected(data: any) {
    this.selectedData = data;
  }
  onRequestAdd() {
    if (this.selectMode == 'facade') {
      this.openModal(this.AddFacadeModal, 0);
    }
    else {
      this.openModal(this.AddPackageModal, 0);
    }
  }

  prepareFacades() {
    ProgressProvider.startLoading();
    let neededData = [];
    var t = this.homeProduct.facades;
    this.rest.GET("spaces?space_type=facade").then(function(data: any) {
      for (var i = 0; i < data.data.spaces.length; i++) {
        let exist = false;
        for (var j = 0; j <  t.length; j++) {
          if (t[j] === data.data.spaces[i].space_id) {
            exist =  true;
            break;
          }
        }
        if (!exist) {
          let _data = new PopupSearchData();
          _data.display =  data.data.spaces[i].space_name;
          _data.id = data.data.spaces[i].space_id;
          neededData.push(_data);
        }
      }
      ProgressProvider.stopLoading();
    });
    this.loadedData = neededData;
  }

  preparePackages() {
    ProgressProvider.startLoading();
    let neededData = [];
    var t = this.homeProduct.packages;
    this.rest.GET("packages").then(function(data: any) {
      console.log(data);
      for (var i = 0; i < data.data.packages.length; i++) {
        let exist = false;
        for (var j = 0; j <  t.length; j++) {
          if (t[j] === data.data.packages[i].package_id) {
            exist =  true;
            break;
          }
        }
        if (!exist) {
          let _data = new PopupSearchData();
          _data.display =  data.data.packages[i].package_name;
          _data.id = data.data.packages[i].package_id;
          neededData.push(_data);
        }
      }
      ProgressProvider.stopLoading();
    });
    this.loadedData = neededData;
  }

  facadeSelected() {  
    // if (this.selectMode == 'facade') {
    //   this.facades.push(this.selectedData);
    //   this.homeProduct.facades.push(this.selectedData.id);
    //   console.log(this.facades);
    // }
    // else {
    //   this.packages.push(this.selectedData);
    //   this.homeProduct.packages.push(this.selectedData.id);
    //   console.log(this.packages);
    // }
    
    this.bsModalRef.hide();
  }
}



