// add room -------------------------
@Component({
  selector: 'modal-add-room',
  templateUrl: './widget-template/add-room/add-room.html'
})
export class ModalAddRoomComponent implements OnInit {
  title: string;
  filename: string;
  loadedData = environment.roomTypeList;  
  searchData = [];
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared) {
  } 

  ngOnInit() {
    this.prepareData();
  }

  prepareData() {
    for (var i = 0; i < this.loadedData.length; i++) {
      let _d = new PopupSearchData();
      _d.display = this.loadedData[i];
      _d.id = i;
      this.searchData.push(_d);
    }
  }

  onChange(event) {
    var files = event.srcElement.files;
    this.filename = files[0].name;
  }

  adjust: string = '';
  normal: string = 'normal';
  addedButtonHidden: boolean = true;
  defaultButtonHidden: boolean = false;
  roomTypeInput: string;

  checkEmpty() {
    if (this.roomTypeInput != '') {
      this.addedButtonHidden = false;
      this.defaultButtonHidden = true;
      this.adjust = 'adjust';
      this.normal = '';
    }
    else if (this.roomTypeInput.length == 0 || this.roomTypeInput == null) {
      this.addedButtonHidden = true;
      this.defaultButtonHidden = false;
      this.adjust = '';
      this.normal = 'normal';
      this.roomTypeInput = null;
    }
  }

  str: string;
  onItemSelected(data: any) {
    return this.str = data;
  }

  roomName: string;
  dimension: number;
  imageFile: any;
  submit(roomName, dimension, imageFile) {
    this.dataPass.dataSubmitLevel(this.str, roomName, dimension, imageFile);
    this.bsModalRef.hide();
  }

  closeInput() {
    this.addedButtonHidden = true;
    this.defaultButtonHidden = false;
    this.adjust = '';
    this.normal = 'normal';
    this.roomTypeInput = '';
  }

  @ViewChild("input") el: ElementRef
  saveRoomType(roomType) {
    if (roomType !== '') {
      this.loadedData.push(roomType);
      this.closeInput();
    }
  }

  deleteRoomType(i) {
    this.loadedData.splice(i, 1);
    return false;
  }

  editRoomType() {

  }

  addRoomType() {
    this.addedButtonHidden = false;
    this.defaultButtonHidden = true;
    this.adjust = 'adjust';
    this.normal = '';
  }
}

