// add features modal-------------------------
@Component({
  selector: 'modal-add-features',
  templateUrl: './widget-template/add-features/add-features.html'
})
export class ModalAddFeaturesComponent {
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared) {
  }

  featureList: any[] = this.dataPass.featureList;
  filename: string;
  // used methods  

  hightlightStatus: boolean[] = [];
  checkboxValue: boolean = false;
  classAll: string;
  listDecoy: string[];
  newFunction() {
    if (!this.checkboxValue) { this.classAll = ''; this.listDecoy = []; }
    else { this.classAll = 'selectedAll'; this.listDecoy = this.dataPass.featureList; }
  }

  listItem: string[] = [];
  submit() {
    this.dataPass.featureInputList = this.listItem;
    if(this.listDecoy.length == this.dataPass.featureList.length){
      this.dataPass.featureInputList = this.listDecoy;
    }     
    this.modalService.onHide;
  }

  // temporary on selected item ------------
  getItem(bool,item,index) {
    if(bool) {
      this.listItem.push(item);
    }
    else {
      this.deleteItemInput(item);
    }    
  }

  deleteItemInput(item: string) {
    const index: number = this.listItem.indexOf(item);
    if (index !== -1) {
      this.listItem.splice(index, 1);
    }
  }
  // ---------------------------------------
}

