
import  NProgress  from 'nprogress';
    
import { EventEmitter, Injectable } from '@angular/core';

@Injectable()
export class ProgressProvider {

    constructor() {  }

    static startLoading() {
        NProgress.start();
    }

    static stopLoading() {
        NProgress.done();
    }

    static testLoading(duration: number) {
        NProgress.start();
        setTimeout(() => {
            NProgress.done();
        }, duration * 1000);
    }
}


