import { Pipe, PipeTransform , Component, Input, Output, ViewEncapsulation, group, EventEmitter, TemplateRef, ViewChild } from '@angular/core';
import { PopupSearchData } from '../../../../model/popp-search/popup-search-model';

@Pipe({
    name: 'searchFilter',
    pure: false
})
export class SearchFilter implements PipeTransform  {
    transform(items: any[], keys: string) {
       let url = window.location.href.split('/')[window.location.href.split('/').length - 1];
       switch (url) {
           case "homeproduct":
            return keys 
                ? items.filter(item =>  item.home_design_name.toLocaleLowerCase().indexOf(keys) !== -1 || 
                item.design_code.toLocaleLowerCase().indexOf(keys) !== -1 || 
                item.levels.toString().toLocaleLowerCase().indexOf(keys) !== -1)
                : items;

            case "create":
            keys = keys ? keys.toLocaleLowerCase() : keys;
            return keys 
                ? items.filter(item =>  item.display.toLocaleLowerCase().indexOf(keys) !== -1)
                : items;

           default:
           return items;
       }
    }
}

@Component({
 selector: 'popup-search-component',
 templateUrl: './popup-search-component.html',
 styleUrls: ['./popup-search-component.scss'],
 encapsulation: ViewEncapsulation.None
})
export class PopupSearchComponent {
    selectedData = "";

    @Input() group_style = false;
    @Input() loadedData: PopupSearchData[] = [];
    @Input() showIcon = true;
    @Input() widthExp:boolean = true;

    @Output() OnRequestAdd: EventEmitter<any> = new EventEmitter();
    @Output() OnItemSelected: EventEmitter<any> = new EventEmitter();

    @ViewChild('popover') popover; // the popover element

    constructor() {}

    selectItem(data: any) {
        this.selectedData = data.display;
        this.OnItemSelected.emit(data);
        this.popover.hide();
    }
    requestAdd() {
        this.OnRequestAdd.emit(null);
        this.popover.hide();
    }    
}



